.subrnd1 {
  overflow: hidden;
  position: absolute;
  top: -20%;
  width: 100%;

  >div {
    position: absolute;
    font-size: 10px;

    &:nth-child(1) {
      font-size: 12px;
      font-weight: 500;
      padding-left: 10px;
      border-left: 1px black solid;
      top: 53%;
      left: 10%;
    }

    &:nth-child(2) {
      top: 72%;
      left: 14%;
    }

    &:nth-child(3) {
      top: 88%;
      left: 9%;
    }

    &:nth-child(4) {
      // border: 1px red solid;
      display: flex;
      justify-content: space-between;
      width: 80%;

      font-size: 10px;
      font-style: normal;
      font-weight: bold;
      font-size: 10px;
      line-height: 139.8%;
      bottom: 2.5%;
      left: 10%;
      // background: #0032A0;

      // padding: 5% 6% 8% 6%;
      // color: rgba(255, 255, 255, 0.6);
      >div {
        display: flex;
        align-items: center;

        >div:nth-child(1) {
          font-family: "Montserrat";
          font-style: normal;
          font-weight: normal;
          font-size: 10px;
          line-height: 12px;

          color: #121212;
        }

        >div:nth-child(2) {
          margin-left: 20px;
          font-family: "Noto Sans KR";
          font-style: normal;
          font-weight: bold;
          font-size: 10px;
          line-height: 139.8%;
          color: #121212;
        }

        >img {
          width: 40px;
        }
      }
    }
  }

  .back-btn {
    // border: 1px red solid;
    // box-sizing: border-box;
    z-index: 1;
    position: absolute;
    top: 15%;
    left: 10%;
    width: 10vw;
    height: 10vw;
  }

  .subrnd1-img1 {
    margin-left: 10%;
    position: absolute;
    top: 12%;
    width: 80%;
  }

  .subrnd1-img2 {
    width: 100%;
  }
}